  }
  .info{
    display: inline-block;
  }
  .header{
    padding: px;
    text-align: center;
    width: 250%;
   }
  .header h1{
   font-size:22px;
   text-align:center;
   display: flex;
   word-wrap: break-word;
   text-indent:5px 
 }
