.second-onboarding{
    text-align: center;
    width: 100%;
}
.onboarding-2 .contents{
    position: relative;
    bottom: 5rem;
    color: black;
}
.content-details{
   position: relative;
   left: 25%;
   bottom: 50px
}
.header-text{
    line-height: 40px;

}
.header-text h1{
    font-weight: 700;
    font-size: 24px;
}
.header-text p{
    font-weight: 400;
    font-size: 18px;
}
fieldset{
    border: 1px solid rgba(243, 114, 63, 1);
    border-radius: 10px;
    height: 70vh;
    width: 50%;

}
.items{
    padding: 25px;
}
.link-container{
    padding: 50px;
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
}
.links{
    width: 100%;
    border: 0.5px solid rgba(243, 114, 63, 1);
    border-radius: 10px;
    padding: 30px;
    font-size: 20px;
    cursor: pointer;
    text-decoration: none;
    color: black;
    transition: 0.5s;
}
.links:hover{
    transform:translateX(20px) ;
  }
  
.link-items{
    display: flex;
    justify-content: space-between;
}
.link-items span{
    position: relative;
    top: 15px;
      
}
.link-items .arrow{
    position: relative;
    right: 50px;
}
@media  (max-width: 800px) {
      .content-image img{
        max-width:50%
      }
      .onboarding-2 .contents{
        text-align: center;
        position: relative;
        padding: 30px;
       
      }
      fieldset{
        width: 100%;
      }
      .content-details{
        position: relative;
        left: 0%;
        bottom: 50px
     }
     .header-text{
        padding: 20px;
     }
     .header-text h1{
        font-weight: 700;
        font-size: 30px;
    }
    .header-text p{
        display: inline-block;
        width: 100%;
        font-size: 15.5px;
        text-align: center;
    }
    .items{
        padding: 20px;
    }
    .link-container{
        padding: 20px;
        display: flex;
       
    }
    .links{
        font-size: 15px;
    }
    .link-items{
        display: flex;   
    }
    .link-items .arrow{
        position: relative;
        right: 0px;
    }
    .link-items span{
        position: relative;
        padding: 7px;
          
    }

}
